<template>
    <div class="appiontList">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png">
            </span>
            <span class="usersTitle">报名成员</span>
            <span class="back"></span>
        </div>
        <ul class="students">
            <li class="student" v-for="(student, index) in students" :key="index">
                <span class="stuInfo">
                    <span class="headPic">
                        <img :src="student.fPhoto">
                    </span>
                    <span class="stuName">{{ student.fNickname }}</span>
                </span>
                <button @click="ensure(index)" class="ensureBtn" :class="student.fLogStatus === '0' ? 'ensured' : 'toEnsure'">{{ student.fLogStatus === '0' ? '已确认' : '待确认'}}</button>
            </li>
        </ul>
    </div>
</template>

<script>
import store from '../../vuex/store'
import { getAppiontlist } from '../../api/getdata'
export default {
  name: 'appiontList',
  data () {
    return {
      students: []
    }
  },
  mounted () {
    this.getDataList()
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    getDataList () {
      let data = getAppiontlist({lessonId: store.state.appiontId})
      data.then(res => {
        console.log(res)
        this.students = res.data
      })
    },
    ensure (index) {
      if (this.students[index].fLogStatus === '-1') {
        this.students[index].fLogStatus = '0'
      }
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.48rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .students {
    list-style: none;
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .student {
    display: flex;
    padding: 0.3rem 0;
    align-items: center;
    justify-content: space-between;
    border-bottom: 0.05rem solid #f5f5f5;
  }

  .stuInfo {
    display: flex;
    align-items: center;
  }

  .stuName {
    margin-left: 0.4rem;
  }

  .headPic {
    width: 1.6rem;
    height: 1.6rem;
    display: inline-block;
    border-radius: 50%;
    position: relative;
  }

  .headPic img {
    border-radius: 0.8rem;
  }

  .ensureBtn {
    width: 1.8rem;
    height: 0.8rem;
    background: #fff;
    border-radius: 0.4rem;
    outline: none;
    font-size: 0.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .ensured {
    border: 1px solid #333333;
    color: #666666;
  }

  .toEnsure {
    border: 1px solid #ffd200;
    color: #ffd200;
  }
</style>



